'use client';

import React, { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { useParams, useRouter } from 'next/navigation';
import { useAccount } from 'wagmi';
import { useEffect } from 'react';
import { useGameState } from '@/hooks/useGameState';
import { useTokenBalance } from '@/hooks/useTokenBalance';
import BuyFacilityModal from '@/components/BuyFacilityModal';
import AccountModal from '@/components/AccountModal';

type Tab = 'RESOURCES' | 'SPACE' | 'SELECTED TILE';

export default function RoomPage() {
  const params = useParams();
  const router = useRouter();
  const { address, isConnected } = useAccount();
  const gameState = useGameState();
  const { apeBalance, bitBalance } = useTokenBalance();
  const [activeTab, setActiveTab] = useState<Tab>('RESOURCES');
  const [isBuyModalOpen, setIsBuyModalOpen] = useState(false);
  const [isProfileModalOpen, setIsProfileModalOpen] = useState(false);

  // Redirect if not connected or trying to access someone else's room
  useEffect(() => {
    if (!isConnected) {
      router.push('/');
    } else if (address && params?.address && 
      (typeof params.address === 'string') && 
      address.toLowerCase() !== params.address.toLowerCase()) {
      router.push(`/room/${address}`);
    }
  }, [isConnected, address, params?.address, router]);

  if (!isConnected || !address) {
    return null;
  }

  const renderTabContent = () => {
    switch (activeTab) {
      case 'RESOURCES':
        return (
          <div className="space-y-2">
            <div className="flex items-center gap-2 font-press-start text-white">
              <span className="text-banana">üíé</span>
              <span>{apeBalance} APE</span>
            </div>
            <div className="flex items-center gap-2 font-press-start text-white">
              <span className="text-banana">ü™ô</span>
              <span>{bitBalance} BIT</span>
            </div>
            <div className="flex items-center gap-2 font-press-start text-white">
              <span className="text-banana">üè†</span>
              <span>0 SPACES LEFT</span>
            </div>
            <div className="flex items-center gap-2 font-press-start text-white">
              <span className="text-banana">‚ö°</span>
              <span>0 GIGAWATTS AVAILABLE</span>
            </div>
          </div>
        );
      case 'SPACE':
        return (
          <div className="font-press-start text-white space-y-4">
            <p>- NO MINING SPACE</p>
            <p>- 0 TOTAL SPACES</p>
            <p>- 0 TOTAL GIGAWATTS</p>
            <p>- CANT MINE WITHOUT SPACE, BUDDY</p>
            <div className="mt-6">
              <button 
                onClick={() => setIsBuyModalOpen(true)}
                className="bg-transparent border-2 border-banana text-banana hover:bg-banana hover:text-royal transition-colors px-6 py-2 font-press-start"
              >
                UPGRADE
              </button>
            </div>
          </div>
        );
      case 'SELECTED TILE':
        return (
          <div className="font-press-start text-white">
            <p>NO TILE SELECTED</p>
            <p>CLICK ON A TILE TO VIEW ITS DETAILS</p>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-royal">
      {/* Header */}
      <header className="nav-bar flex justify-between items-center px-6 py-4">
        <div className="flex items-center">
          <Image
            src="/bitape-logo.svg"
            alt="BitApe Logo"
            width={32}
            height={32}
            className="mr-2 pixel-art"
          />
          <span className="font-press-start text-banana text-xl">bitape</span>
        </div>
        <nav className="flex items-center gap-4">
          <Link href="/about" className="font-press-start text-white hover:text-banana">
            ABOUT
          </Link>
          <Link href="/trade" className="font-press-start text-white hover:text-banana">
            TRADE $BIT
          </Link>
          <Link href="/leaderboard" className="font-press-start text-[#4A5568] hover:text-banana">
            LEADERBOARD
          </Link>
          <button className="font-press-start text-banana border-2 border-banana px-4 py-2 hover:bg-banana hover:text-royal pixel-button">
            ANNOUNCEMENTS
          </button>
          <button className="font-press-start text-banana border-2 border-banana px-4 py-2 hover:bg-banana hover:text-royal pixel-button">
            REFER A FRIEND
          </button>
          <button 
            onClick={() => setIsProfileModalOpen(true)}
            className="font-press-start text-banana border-2 border-banana px-4 py-2 hover:bg-banana hover:text-royal pixel-button"
          >
            PROFILE
          </button>
        </nav>
      </header>

      <div className="grid grid-cols-12 gap-6 p-6">
        {/* Left Column */}
        <div className="col-span-4">
          {/* Resources Panel */}
          <div className="panel p-4 mb-6">
            <div className="flex gap-4 font-press-start text-white mb-4">
              {(['RESOURCES', 'SPACE', 'SELECTED TILE'] as const).map((tab) => (
                <button
                  key={tab}
                  onClick={() => setActiveTab(tab)}
                  className={`${activeTab === tab ? 'text-banana' : 'text-[#4A5568]'}`}
                >
                  {tab}
                </button>
              ))}
            </div>
            {renderTabContent()}
          </div>

          {/* Stats Panel */}
          <div className="panel p-4 mb-6">
            <div className="flex gap-4 font-press-start text-white mb-4">
              <button className="text-banana">SIMPLE</button>
              <button className="text-[#4A5568]">PRO</button>
            </div>
            <div className="space-y-2 font-press-start text-white">
              <p>- YOU ARE MINING {gameState.miningRate} BitApe A DAY</p>
              <p>- YOUR HASH RATE IS {gameState.hashRate} GH/S</p>
              <p>- {gameState.blocksUntilHalving} BLOCKS UNTIL NEXT HALVENING</p>
              <p>- YOU HAVE {gameState.networkHashRatePercentage}% OF THE TOTAL NETWORK HASH RATE ({gameState.totalNetworkHashRate} GH/S)</p>
            </div>
          </div>

          {/* Mining Panel */}
          <div className="panel p-4">
            <div className="text-center font-press-start">
              <p className="text-white mb-4">YOU HAVE MINED {gameState.minedBit} $BIT</p>
              <button 
                onClick={() => gameState.claimReward()}
                disabled={gameState.isClaimingReward || !gameState.hasFacility}
                className="w-full bg-royal text-banana py-2 px-4 hover:bg-royal-dark disabled:opacity-50 pixel-button"
              >
                CLAIM MINED $BIT
              </button>
            </div>
          </div>
        </div>

        {/* Right Column */}
        <div className="col-span-8">
          <div className="panel p-8 h-full flex flex-col items-center justify-center">
            {!gameState.hasFacility ? (
              <>
                <h2 className="font-press-start text-white text-2xl mb-8">YOU DON'T HAVE A SPACE TO MINE!</h2>
                <button
                  onClick={() => setIsBuyModalOpen(true)}
                  className="font-press-start text-banana border-2 border-banana px-8 py-4 hover:bg-banana hover:text-royal pixel-button"
                >
                  BUY FACILITY
                </button>
              </>
            ) : (
              <div className="w-full h-full flex items-center justify-center">
                <span className="font-press-start text-white">Your mining facility will appear here</span>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* BIGBOY 9000 Label */}
      <div className="absolute bottom-4 right-4">
        <span className="font-press-start text-banana">BIGBOY 9000</span>
      </div>

      {/* Buy Facility Modal */}
      <BuyFacilityModal 
        isOpen={isBuyModalOpen}
        onClose={() => setIsBuyModalOpen(false)}
        onConfirm={() => {
          // Add your confirm logic here
          setIsBuyModalOpen(false);
        }}
      />

      {isConnected && address && (
        <AccountModal
          isOpen={isProfileModalOpen}
          onClose={() => setIsProfileModalOpen(false)}
          address={address}
          apeBalance={apeBalance}
          bitBalance={bitBalance}
        />
      )}
    </div>
  );
} 